const STORAGE_KEY = 'driver_mobile_user_info_key'

export default {

    urlParam(name) {
        let reg = `(^|&)${name}=([^&]*)(&|$)`
        let r = window.location.search.substr(1).match(reg);
        if (r != null) return decodeURI(r[2]);
        return null;
    },
    fetch() {
        const data = window.JSON.parse(window.localStorage.getItem(STORAGE_KEY) || '{}')
        window.console.log('fetch', data)
        return data;
    },

    save(items) {
        window.localStorage.setItem(STORAGE_KEY, window.JSON.stringify(items))
    }

}

